import React, { useRef, useState, useEffect } from "react";
import logo from "./logo.svg";
import "./App.scss";
import Navbar from "./components/navbar";
import Hero from "./sections/hero";
import Skills from "./sections/skills";
import Projects from "./sections/projects";
import Contact from "./sections/contact";
import BgCanvas from "./components/bgCanvas";
import { act } from "@testing-library/react";

const useStateRef = (initialData) => {
  const [state, _setState] = useState(initialData);
  const stateRef = useRef(state);

  const setState = data => {
    stateRef.current = data;
    _setState(data);
  };

  return [stateRef, setState]
}

function App() {
  const hero = useRef(null);
  const skills = useRef(null);
  const projects = useRef(null);
  const contact = useRef(null);
  const startRef = useRef(null);
  const endRef = useRef(null);
  const [state, setState] = useState({
    scrollPosition: 0,
    sections: [
      { id: 0, name: "Hero", component: <Hero />, ref: hero, active: false },
      { id: 1, name: "Skills", component: <Skills />, ref: skills, active: false },
      { id: 2, name: "Projects", component: <Projects />, ref: projects, active: false },
      { id: 3, name: "Contact", component: <Contact />, ref: contact, active: false },
    ],
    currentSection: 0,
  });

  const [blockRef, setBlock] = useStateRef(false);
  useEffect(() => {
    console.log(blockRef);
  }, [blockRef]);

  useEffect(() => {
    if (window.scrollY == 0) {
      let hero = state.sections.find((x) => x.id == 0);
      hero.ref.current.scrollIntoView({
        behavior: "auto",
        block: "start",
      });
    }
  }, []);

  function HandleScroll(e) {
    if (blockRef.current) {
      return;
    }

    console.log(blockRef.current);

    const scrollPosition = window.scrollY;
    const windowHeight = window.innerHeight;

    let contact = state.sections.find((x) => x.id == 3);
    if (scrollPosition == 0) {
      contact.ref.current.scrollIntoView({
        behavior: "auto",
        block: "start",
      });

      setState({
        ...state,
        sections: state.sections.map((section) => {
          return section.id == 3
            ? {
                ...section,
                active: true,
              }
            : section;
        }),
      });
      console.log("settingblock");
      setBlock(true);

      setTimeout(() => setBlock(false), 40);

      return;
    }

    let { height, offsetTop } = getDimensions(contact.ref.current);
    let contactEnd = offsetTop + height;
    // console.log(contactEnd, scrollPosition + windowHeight);
    if (scrollPosition + windowHeight >= contactEnd) {
      if (startRef.current != null) {
        let { height } = getDimensions(startRef.current);
        window.scrollTo(0, height - windowHeight);

        console.log("settingblock");
        setBlock(true);
        setTimeout(() => setBlock(false), 40);
      }
    }

    let sectionChanged = false;
    let sections = state.sections.map((x) => {
      let ele = x.ref.current;
      if (ele) {
        const { height, offsetBottom, offsetTop } = getDimensions(ele);
        let isSelected =
          scrollPosition + windowHeight / 2 >= offsetTop && scrollPosition + windowHeight / 2 < offsetBottom;

        return isSelected != x.active ? { ...x, active: isSelected } : x;
      }
    });

    setState({
      ...state,
      scrollPosition: scrollPosition,
      sections: [...sections],
      // currentSection: activeSection.id,
    });

    // }
  }

  useEffect(() => {
    HandleScroll();
    window.addEventListener("scroll", HandleScroll);
  }, []);

  function RenderSections(params) {
    let maxIndex = state.sections.length - 1;

    return (
      <div className="sections">
        <section id={state.sections[maxIndex].name} ref={startRef}>
          {state.sections[maxIndex].component}
        </section>
        {state.sections.map((section) => (
          <section id={section.name} ref={section.ref}>
            {section.component}
          </section>
        ))}
        {/* <section id={0} ref={endRef}>
          {state.sections[0].component}
        </section> */}
      </div>
    );
  }

  return (
    <div className="app">
      <Navbar sections={state.sections} />
      <BgCanvas scrollPosition={state.scrollPosition} />
      {RenderSections()}
      {/* <div className="sections">
        {state.sections.map((section) => (
          <section id={section.name} ref={section.ref}>
            {section.component}
          </section>
        ))}
      </div> */}
    </div>
  );
}

const getDimensions = (ele) => {
  const { height } = ele.getBoundingClientRect();
  const offsetTop = ele.offsetTop;
  const offsetBottom = offsetTop + height;

  return {
    height,
    offsetTop,
    offsetBottom,
  };
};

export default App;
